/* font-family: 'Nunito', sans-serif; */
[type=reset], [type=submit], button, html [type=button] {
  -webkit-appearance: button;
}

body {
  padding:0px;
  margin: 0px;
  background-color: #fafafa;
  font-family: 'Nunito', sans-serif;
}
.header {
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 30px;
  max-width: 1118px;
  margin: 0 auto;
  background-color: #fff;
  margin-bottom: 50px;
}
.logo {
  height: 28px;
  width: auto;
}
.btn {
  color: #45bceb;
  background-color: transparent;
  border:1px solid #45bceb;
  text-shadow: none;
  padding: 0 14px;
  font-size: 14px;
  line-height: 30px;
  height: 35px;
  box-shadow: 0 2px 0 rgba(0,0,0,.035);
  outline: 0;
  border-radius: 4px;
}
.headerRight {
  display: flex;
  align-items: center;
  font-size: 14px;
}
.headerRight  button {
  margin-left: 25px;
}

.loan {
  margin: auto;
  max-width: 1118px;
  background: #f5f5f5;
  padding: 20px 40px;
}

.template {
  margin: auto;
  max-width: 1118px;
  padding: 20px 40px;
}

.text-center {
  text-align: center;
}

.line {
  width: 95%;
  margin: auto;
  height: 1px;
  background-color: #ccc;
  display: inline-block;
}

.clear {
  clear: both;
}

.app-col-6  {
  padding: 20px;
   width: 50%;
   float: left;
}

.app-col-3  {
  padding: 20px;
 width: 50%;
 float: left;
}

.form-row {
  margin-bottom: 20px; 
}
.form-row label {
  display: block;
  line-height: 30px;
  margin-bottom: 6px;
  font-family: 'Nunito', sans-serif;
  font-weight: 600;
  font-size: 14px;
  color: #252525;
}
.form-row input, .form-row select {
  height: 50px;
  padding: 15px 20px;
  font-size: 16px;
  line-height: 20px;
  width: 100%;
  border: none;
  overflow: visible;
  border: none;
  box-shadow:  0 0 2px 0 rgba(0,0,0,.05),0 2px 2px 0 rgba(0,0,0,.15);
}

.submit-btn {
  font-weight: 400;
  text-align: center;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  border: 1px solid transparent;
  white-space: nowrap;
  font-size: 18px;
  line-height: 30px;
  padding: 10px 30px;
  height: 50px;
  border-radius: 4px;
  display: inline-block;
  color: #fff;
  background-color: #45bceb;
  border-color: #45bceb;
  text-shadow: 0 -1px 0 rgba(0,0,0,.12);
  box-shadow: 0 2px 0 rgba(0,0,0,.035);
  width: auto;
  margin-left: -60px;
  margin-top: 20px;
}

/* widget */


.widget {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  min-width: 100%;
  color: #101010;
  background: -webkit-gradient(linear, left top, right top, from(#009ccb), to(rgba(0,246,46,0.95)));
  background: linear-gradient(to right, #F1F2B5, #267871);
  -webkit-box-shadow: 1px 3px 10px #111;
          box-shadow: 1px 3px 10px #111;
  -webkit-animation: fade-in 0.4s forwards;
          animation: fade-in 0.4s forwards;
}
.widget>* {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto;
}
.widget p {
  margin: 0px;
}
.widget__inner {
  position: relative;
  padding: 10px;
  margin: 15px auto;
}
.widget__inner:nth-of-type(1) {
  -ms-flex-preferred-size: 40%;
      flex-basis: 40%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0 10px 0 0;
}
.widget__inner:nth-of-type(1) .icon {
  font-size: 65px;
  color: #fff;
  text-shadow: 1px 2px 1px #212121;
}
.widget__inner:nth-of-type(1) p {
  width: 100%;
  text-align: right;
}
.widget__inner:nth-of-type(1):after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  height: 100%;
  width: 1px;
  border-right: 1px dashed #5b5b5b;
}
.widget__inner:nth-of-type(2) {
  -ms-flex-preferred-size: 20%;
      flex-basis: 20%;
}
.widget__inner:nth-of-type(2) .temp {
  font-size: 60px;
  line-height: 1.1;
  font-weight: bold;
}
.widget__inner:nth-of-type(2) .temp-range {
  font-size: 21px;
}
.widget__inner:nth-of-type(3) {
  -ms-flex-preferred-size: 40%;
      flex-basis: 40%;
}
.widget__inner:nth-of-type(3) p {
  font-size: 21px;
  line-height: 1.5;
}
.widget__inner:nth-of-type(3) p span:not(.icon) {
  font-size: 14px;
}
.widget__inner:nth-of-type(3) i {
  width: 30px;
  margin-right: 5px;
  text-align: center;
}
.icon {
  display: inline-block;
  vertical-align: top;
  font-size: 24px;
}
p.status {
  font-size: 16px;
  font-weight: 700;
  text-align: right;
}
.slick-next:before, .slick-prev:before {
  color: #45bcea;
}
p.TimePeriod {
  position: absolute;
  bottom: 0px;
}
.slick-slide > div {
  margin: 0px 10px;
}
